'use strict';

/**
 * Helpers object
 *
 * @type {Object}
 */
var helpers = {};

/**
 * Create a new list item for the main menu
 *
 * @method createMenuItem
 * @param  {Object} data The current post or page of the page
 * @return {Node} A new list item of the main menu
 */
helpers.createMenuItem = function (data) {
  var listItemEl = document.createElement('li');

  listItemEl.appendChild(helpers.createLink(data));

  return listItemEl;
};

/**
 * Create a new anchor
 *
 * @method createLink
 * @param  {Object} data The current post or page of the page
 * @return {Node} The complete anchor for the current post or page
 */
helpers.createLink = function (data) {
  var pageAnchor = document.createElement('a'),
      pageAnchorContent = document.createTextNode(data.title);

  if ('home' === data.slug) {
    pageAnchor.href = '#';
  } else {
    pageAnchor.href = '#' + data.slug;
  }

  pageAnchor.appendChild(pageAnchorContent);

  return pageAnchor;
};

/**
 * Get the main menu element
 *
 * @method getMenu
 * @return {Node} The main menu for the site
 */
helpers.getMenu = function () {
  return document.querySelector('#mainNav ul');
};

/**
 * Get the title of the current page
 *
 * @method getPageTitle
 * @return {Node} The title of the current page
 */
helpers.getPageTitle = function () {
  return document.getElementById('pageTitle');
};

/**
 * Get the content container of the current page
 *
 * @method getContentContainer
 * @return {Node} The content container for the current page
 */
helpers.getContentContainer = function () {
  return document.getElementById('pageContent');
};

/**
 * Gets the Editor element in the DOM
 *
 * @return {Object} Main editor DOM object
 */
helpers.getEditorEl = function () {
  return document.getElementById('editor');
};

/**
 * Gets Editor toggle element in the DOM
 *
 * @return {Object} Main toggle element
 */
helpers.getEditorToggle = function () {
  return document.getElementById('editorToggle');
};

/**
 * Gets editor toggle link Element in the DOM
 *
 * @return {Object} Main toggle link
 */
helpers.getEditorToggleLink = function () {
  return document.querySelector('#editorToggle a');
};

/**
 * Get the title input element from the editor
 *
 * @return {Object} Editor title input
 */
helpers.getEditorTitleEl = function () {
  return document.getElementById('editTitle');
};

/**
 * Get the content textarea from the editor
 *
 * @return {Object} Editor textarea
 */
helpers.getEditorContentEl = function () {
  return document.getElementById('editContent');
};

/**
 * Get the editor button
 *
 * @return {Object} Editor button
 */
helpers.getEditorUpdate = function () {
  return document.getElementById('editUpdateBtn');
};
'use strict';

/**
 * Model object
 *
 * @type {Object}
 */
var model = {};

/**
 * Initialize the Model
 *
 * @method init
 */
model.init = function () {
  model.setLocalStorage(data);
};

/**
 * Get the content that is currently on the page
 *
 * @method getContent
 * @return {[type]} [description]
 */
model.getContent = function (slug) {
  var content = model.getPost(slug);

  if (null === content) {
    content = model.getPage(slug);
  }

  if (null === content) {
    content = {
      title: '404 Error',
      content: 'Content not found'
    };
  }

  return content;
};

/**
 * Determine what post or page is currently being viewed, and get the content for that page
 *
 * @method getCurrentContent
 * @return {Object} The content of the current post or page
 */
model.getCurrentContent = function () {
  var slug = router.getSlug(),
      content = void 0;

  if (null === slug) {
    slug = 'home';
  }

  content = model.getContent(slug);

  return content;
};

/**
 * Get posts data from the browsers local storage, and sets to a variable for use
 *
 * @method getPosts
 * @return {Array} An array of post objects
 */
model.getPosts = function () {
  var posts = model.getLocalStorage().posts;

  return posts;
};

/**
 * Get a single post based upon the current URL slug
 *
 * @method getPost
 * @param  {String} slug The current URL slug
 * @return {Object}      The post object with a matching slug, or null if there is no match
 */
model.getPost = function (slug) {
  var posts = model.getLocalStorage().posts;

  for (var i = 0; i < posts.length; i++) {
    if (slug === posts[i].slug) {
      return posts[i];
    }
  }

  return null;
};

/**
 * Get pages data from the browsers local storage, and sets to a variable for use
 *
 * @method getPages
 * @return {Array} An array of page objects
 */
model.getPages = function () {
  var pages = model.getLocalStorage().pages;

  return pages;
};

/**
 * Get a single page based upon the current URL slug
 *
 * @method getPage
 * @param  {String} slug The current URL slug
 * @return {Object}      The page object with a matching slug, or null if there is no match
 */
model.getPage = function (slug) {
  var pages = model.getLocalStorage().pages;

  for (var i = 0; i < pages.length; i++) {
    if (slug === pages[i].slug) {
      return pages[i];
    }
  }

  return null;
};

/**
 * Update content
 *
 * @method updateContent
 * @param  {[type]}      content [description]
 * @return {[type]}              [description]
 */
model.updateContent = function (content) {
  var storage = model.getLocalStorage(),
      date = new Date();

  if ('post' === content.type) {
    storage.posts.forEach(function (post) {
      if (content.id === post.id) {
        post.title = content.title;
        post.content = content.content;
        post.modified = date.toISOString();
      }
    });
  }

  if ('page' === content.type) {
    storage.pages.forEach(function (page) {
      if (content.id === page.id) {
        page.title = content.title;
        page.content = content.content;
        page.modified = date.toISOString();
      }
    });
  }

  model.setLocalStorage(storage);
};

/**
 * Get content from the browsers local storage
 *
 * @method getLocalStorage
 * @return {Object} JavaScript object or array containing the stored site data
 */
model.getLocalStorage = function () {
  return JSON.parse(localStorage.getItem('vanillaPress'));
};

/**
 * Save the temporary data to the browsers local storage
 *
 * @method setLocalStorage
 * @param  {sting} data JSON string of the data to be stored
 */
model.setLocalStorage = function (data) {
  localStorage.setItem('vanillaPress', JSON.stringify(data));
};

/**
 * Deletes data from the browsers local storage
 *
 * @method deleteLocalStorage
 */
model.deleteLocalStorage = function () {
  localStorage.removeItem('vanillaPress');
};
'use strict';

/**
 * Router object
 *
 * @type {Object}
 */
var router = {};

/**
 * Initialize the Router
 *
 * @method init
 * @return {method} The functions to call when the Router loads
 */
router.init = function () {
  router.loadContent();
  router.listenForChanges();
};

/**
 * Get the slug from the URL
 *
 * @method getSlug
 * @return {String} The slug for the current post or page
 */
router.getSlug = function () {
  var slug = window.location.hash;

  if ('' === slug) {
    return null;
  } else {
    return slug.substring(1);
  }
};

/**
 * Listen for changes to the URL hash
 *
 * @method listenForChanges
 */
router.listenForChanges = function () {
  window.addEventListener('hashchange', router.loadContent, false);
};

/**
 * Load content based upon the current slug
 *
 * @method loadContent
 * @return {method} The method to run based upon the URL hash
 */
router.loadContent = function () {
  var slug = router.getSlug();

  view.clearContent();

  if (null === slug) {
    view.loadSingle('home');
  } else if ('blog' === slug) {
    view.loadBlogPosts();
  } else {
    view.loadSingle(slug);
  }
};
'use strict';

/**
 * View object
 *
 * @type {Object}
 */
var view = {};

/**
 * Initialize the View
 *
 * @method init
 */
view.init = function () {
  view.createMenu();
};

/**
 * Get the post data from local storage, create markup for each one, and append them to the page
 *
 * @method loadBlogPosts
 */
view.loadBlogPosts = function () {
  var posts = model.getPosts(),
      postsMarkup = document.createDocumentFragment(),
      contentContainer = helpers.getContentContainer();

  for (var i = 0; i < posts.length; i++) {
    postsMarkup.appendChild(view.createPostMarkup(posts[i]));
  }

  contentContainer.appendChild(postsMarkup);
};

/**
 * Load a single blog post
 *
 * @method loadSingle
 * @param  {String} slug The current URL slug
 */
view.loadSingle = function (slug) {
  var content = model.getContent(slug),
      titleEl = helpers.getPageTitle(),
      postContent = helpers.getContentContainer();

  titleEl.innerHTML = content.title;
  postContent.innerHTML = content.content;
};

/**
 * Update the page title with content in the editor
 */
view.updateTitleFromForm = function () {
  var titleEl = helpers.getPageTitle(),
      title = helpers.getEditorTitleEl().value;

  titleEl.innerHTML = title;
  editor.currentContent.title = title;
};

/**
 * Update the page content with content in the editor
 */
view.updateContentFromForm = function () {
  var contentEl = helpers.getContentContainer(),
      content = helpers.getEditorContentEl().value;

  contentEl.innerHTML = content;
  editor.currentContent.content = content;
};

/**
 * Clear the content from the page
 *
 * @method clearContent
 */
view.clearContent = function () {
  var titleEl = helpers.getPageTitle(),
      postContent = helpers.getContentContainer();

  titleEl.innerHTML = '';
  postContent.innerHTML = '';
};

/**
 * Display the menu of pages
 *
 * @method createMenu
 */
view.createMenu = function () {
  var pages = model.getPages(),
      menuMarkup = document.createDocumentFragment(),
      menuEl = helpers.getMenu();

  for (var i = 0; i < pages.length; i++) {
    menuMarkup.appendChild(helpers.createMenuItem(pages[i]));
  }

  menuEl.appendChild(menuMarkup);
};

/**
 * Create the markup for the posts
 *
 * @method createPostMarkup
 * @param  {Object} post Post to create markup for
 * @return {Node} Final post markup
 */
view.createPostMarkup = function (post) {
  var articleEl = document.createElement('article'),
      titleEl = document.createElement('h2'),
      titleAnchor = document.createElement('a'),
      titleContent = document.createTextNode(post.title),
      postContent = document.createElement('div');

  titleAnchor.appendChild(titleContent);
  titleAnchor.href = '#' + post.slug;
  titleEl.appendChild(titleAnchor);

  postContent.appendChild(document.createTextNode(post.content));

  articleEl.appendChild(titleEl);
  articleEl.appendChild(postContent);

  return articleEl;
};
'use strict';

/**
 * Editor object
 *
 * @type {Object}
 */
var editor = {};

editor.currentContent = '';

/**
 * Initialize the editor
 *
 * @method init
 */
editor.init = function () {
  editor.listener();
};

/**
 * Update contentObj
 *
 * @method updateContent
 * @return {[type]}      [description]
 */
editor.updateContent = function () {
  model.updateContent(editor.currentContent);
};

/**
 * Fill the editor form with the content of the current post or page
 *
 * @method fillEditorForm
 * @param  {Obejct} content Content object for the current page or post
 */
editor.loadEditorForm = function (content) {
  var formTitle = helpers.getEditorTitleEl(),
      formContent = helpers.getEditorContentEl();

  formTitle.value = content.title;
  formContent.value = content.content;

  editor.addFormListeners();
};

/**
 * Listen to the form inputs for content changes
 */
editor.addFormListeners = function () {
  var formTitle = helpers.getEditorTitleEl(),
      formContent = helpers.getEditorContentEl(),
      formButton = helpers.getEditorUpdate();

  formTitle.addEventListener('input', view.updateTitleFromForm, false);
  formContent.addEventListener('input', view.updateContentFromForm, false);
  formButton.addEventListener('click', editor.updateContent, false);
};

/**
 * Listens for the editor toggle button
 *
 * @return {Object} Main editor DOM object
 */
editor.listener = function () {
  var toggle = helpers.getEditorToggle();

  toggle.addEventListener('click', function () {
    editor.toggle();
    event.preventDefault();
  }, false);
};

/**
 * Controls the toggle for the editor
 *
 * @return {Object} Main toggle element
 */
editor.toggle = function () {
  var editorEl = helpers.getEditorEl(),
      editorToggle = helpers.getEditorToggle();

  editor.currentContent = model.getCurrentContent();

  editorEl.classList.toggle('hidden');
  editorToggle.classList.toggle('hidden');

  if (false === editorToggle.classList.contains('hidden')) {
    editor.loadEditorForm(editor.currentContent);
  }
};
"use strict";

/**
 * The primary VanillaPress application object
 *
 * @type {Object}
 */
var vanillaPress = {};

vanillaPress.init = function () {
  model.init();
  router.init();
  view.init();
  editor.init();
};

vanillaPress.init();